<template>
  <div class="site-logo">
    <!-- Logo with text -->
    <v-row
      v-if="hasText"
      align="center"
      justify="start"
      class="site-logo_full pa-2"
    >
      <!-- logo icon -->
      <div class="pl-4 pr-3">
        <v-btn
          fab
          class="site-logo__logo-wrapper"
          :to="siteName ? '/' + siteName : '/'"
        >
          <v-img
            :src="siteName ? siteLogo : '/img/icons/android-icon-192x192.png'"
            class="site-logo__logo-image"
            width="48"
            height="48"
            contain
          ></v-img>
        </v-btn>
      </div>

      <!-- Logo text -->
      <div class="site-logo_full__logo-text text-left pl-4" align="center">
        <div
          class="font-weight-regular text-uppercase"
          v-text="siteName ? siteNameRu : 'АЗИЯ'"
        ></div>
        <div class="font-weight-thin">СЕГОДНЯ</div>
      </div>
    </v-row>

    <!-- Logo icon only -->
    <v-row v-else align="center" justify="center">
      <!-- logo icon -->
      <div class="pl-4 pr-3">
        <v-tooltip
          top
          :disabled="!siteName"
          color="rgba(255, 255, 255, 0)"
          content-class="site-logo__tooltip"
        >
          <template v-slot:activator="{ on }">
            <v-btn
              fab
              class="site-logo__logo-wrapper"
              v-on="siteName ? on : false"
              :to="siteName ? '/' + siteName : '/'"
            >
              <v-img
                :src="
                  siteName ? siteLogo : '/img/icons/android-icon-192x192.png'
                "
                class="site-logo__logo-image"
                width="48"
                height="48"
                contain
              ></v-img>
            </v-btn>
          </template>
          <v-chip v-if="siteName" dark small>{{
            siteNameRu.toUpperCase()
          }}</v-chip>
        </v-tooltip>
      </div>
    </v-row>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  props: {
    siteName: { type: String, default: null },
    hasText: { type: Boolean, default: false },
    backgroundColor: { type: String, default: 'transparent' },
    flat: { type: Boolean, default: false }
  },
  computed: {
    ...mapState(['sites']),
    siteLogo() {
      return `/shared/img/logo-${this.siteName}-icon.png`
    },
    siteNameRu() {
      return this.sites.find(site => site.name === this.siteName).nameRu
    }
  }
}
</script>

<style lang="scss" scoped>
.site-logo {
  .site-logo__logo-wrapper {
    width: 48px;
    height: 48px;
  }

  .site-logo_full {
    width: 220px;

    &__logo-text {
      height: 48px;
      border-left: 2px solid #525252;
      padding-left: 16px;
    }
  }
}
</style>
